import React, { useState } from 'react';
import { useWebSocket } from '../context/WebSocketContext';
import { api } from '../services/api';
import './ControlPanel.css';

const ControlPanel = () => {
  const { sensorData } = useWebSocket();
  const [localThreshold, setLocalThreshold] = useState(sensorData.threshold);
  const [isDragging, setIsDragging] = useState(false);

  React.useEffect(() => {
    if (!isDragging) {
      setLocalThreshold(sensorData.threshold);
    }
  }, [sensorData.threshold, isDragging]);

  const handleSliderChange = (e) => {
    setLocalThreshold(parseInt(e.target.value));
  };

  const handleSetThreshold = async () => {
    try {
      await api.setThreshold(localThreshold);
      alert(` t ngng cnh bo: ${localThreshold} ppm`);
    } catch (error) {
      console.error('Error setting threshold:', error);
      alert('Li khi t ngng: ' + error.message);
    }
  };

  const handleControl = async (controlFn, newState, deviceName) => {
    try {
      await controlFn(newState);
    } catch (error) {
      console.error(`Error controlling ${deviceName}:`, error);
      alert(`Li khi iu khin ${deviceName}: ` + error.message);
    }
  };

  const getButtonText = (state, onText, offText) => {
    return state ? onText : offText;
  };

  return (
    <>
      {/* Controls Section */}
      <section className="controls-section">
        <h2> iu khin thit b</h2>
        <div className="control-group">
          <div className="control-item">
            <div className="control-info">
              <span className="control-icon"></span>
              <div className="control-text">
                <h4>Qut ht</h4>
                <p>Lm sch khng kh</p>
              </div>
            </div>
            <button
              className="control-toggle"
              data-state={sensorData.relay1 ? 'off' : 'on'}
              onClick={() => handleControl(api.controlRelay1, !sensorData.relay1, 'Qut ht')}
            >
              <span className="toggle-text">
                {getButtonText(sensorData.relay1, 'TT', 'BT')}
              </span>
            </button>
          </div>

          <div className="control-item">
            <div className="control-info">
              <span className="control-icon"></span>
              <div className="control-text">
                <h4>My bm</h4>
                <p>H thng cha chy</p>
              </div>
            </div>
            <button
              className="control-toggle"
              data-state={sensorData.relay2 ? 'off' : 'on'}
              onClick={() => handleControl(api.controlRelay2, !sensorData.relay2, 'My bm')}
            >
              <span className="toggle-text">
                {getButtonText(sensorData.relay2, 'TT', 'BT')}
              </span>
            </button>
          </div>

          <div className="control-item">
            <div className="control-info">
              <span className="control-icon"></span>
              <div className="control-text">
                <h4>Ca s</h4>
                <p>Thng gi t ng</p>
              </div>
            </div>
            <button
              className="control-toggle"
              data-state={sensorData.window ? 'off' : 'on'}
              onClick={() => handleControl(api.controlWindow, !sensorData.window, 'Ca s')}
            >
              <span className="toggle-text">
                {getButtonText(sensorData.window, 'NG', 'M')}
              </span>
            </button>
          </div>

          <div className="control-item">
            <div className="control-info">
              <span className="control-icon"></span>
              <div className="control-text">
                <h4>Ci bo ng</h4>
                <p>Cnh bo m thanh</p>
              </div>
            </div>
            <button
              className="control-toggle"
              data-state={sensorData.buzzer ? 'off' : 'on'}
              onClick={() => handleControl(api.controlBuzzer, !sensorData.buzzer, 'Ci bo ng')}
            >
              <span className="toggle-text">
                {getButtonText(sensorData.buzzer, 'TT', 'BT')}
              </span>
            </button>
          </div>
        </div>
      </section>

      {/* Threshold Settings */}
      <section className="settings-section">
        <h2> Ci t ngng</h2>
        <div className="settings-box">
          <div className="setting-display">
            <span className="setting-label">Ngng hin ti</span>
            <span className="setting-value">
              {isDragging ? localThreshold : sensorData.threshold} ppm
            </span>
          </div>
          <button className="btn-primary btn-block" onClick={handleSetThreshold}>
             p dng ngng mi
          </button>
        </div>
      </section>
    </>
  );
};

export default ControlPanel;
